@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desctop {
  @media (min-width: $desctop-min-width) {
    @content;
  }
}

@mixin on-desctop-design {
  @media (min-width: $desctop-design-width) {
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 40px;
  }

  @include on-desctop {
    padding-inline: 54px;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;

  @include on-tablet {
    --columns: 6;

    column-gap: 30px;
    grid-template-columns: repeat(var(--columns), 1fr);
  }

  @include on-desctop {
    --columns: 12;

    column-gap: 30px;
    grid-template-columns: repeat(var(--columns), 1fr);
  }
}
